import { Component } from '@angular/core';
import {
  ActionSheetController,
  IonicPage,
  ModalController,
  NavController,
  NavParams,
  ViewController
} from 'ionic-angular';
import { SpecializationsProvider } from '../../providers/specializations/specializations';
import { SpecializationMapper } from '../../models/specialization.model';

/**
 * Generated class for the SpecializationClinicSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'SpecializationClinicSearchPage'
})
@Component({
  selector: 'page-specialization-clinic-search',
  templateUrl: 'specialization-clinic-search.html',
})
export class SpecializationClinicSearchPage {
  searching: boolean = false;

  specializationName: string;

  currentItems: SpecializationMapper[];

  originalItems: SpecializationMapper[];

  clinicSpecializationSelected: any[] = [];

  constructor(public navCtrl: NavController, private viewCtrl: ViewController,  public navParams: NavParams,
              private actionSheetCtrl: ActionSheetController, public specializationsPrv: SpecializationsProvider,
              public modalCtrl: ModalController) {
    this.clinicSpecializationSelected = this.navParams.get('clinicSpecializationSelected');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SpecializationClinicSearchPage');
    this.initializeItems();
  }

  initializeItems() {
    this.searching = true;
    this.specializationsPrv.query().subscribe((data: SpecializationMapper[]) => {
      this.searching = false;
      this.originalItems = data;
      this.currentItems = this.originalItems;
    });
  }

  onCancel() {
    this.currentItems = this.originalItems;
  }

  getSpecializations(ev: any) {
    const val = ev.target.value;
    // Reset items back to all of the items
    if (!this.originalItems || !this.originalItems.length) {
      return;
    }

    if (val === '' ) {
      this.currentItems = this.originalItems;
    }

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.currentItems = this.originalItems.filter((item: any) => {
        return item.name.toLowerCase().indexOf(val.toLowerCase()) >= 0;
      })
    }
  }

  openClinicOptions(specialization: SpecializationMapper) {
    let options = [];
    for (let clinic of specialization.clinics) {
      options.push({
        text: clinic.name,
        handler: () => {
          this.addClinicAndSpecialization(clinic, specialization)
        }
      })
    }

    let actionSheet = this.actionSheetCtrl.create({
      title: 'Escolha uma cl√≠nica',
      buttons: options
    });

    actionSheet.present();
  }

  dismiss() {
    this.viewCtrl.dismiss({ clinicSpecializationSelected: this.clinicSpecializationSelected });
  }

  addClinicAndSpecialization(clinic: any, specialization: any) {
    this.clinicSpecializationSelected.push({clinic: clinic, specialization: specialization});
  }

  removeClinicSpecializationSelected(index: number) {
    this.clinicSpecializationSelected.splice(index, 1);
  }

}



